/* ==================================
	Font
/* =================================*/
@import '../fonts/Gilroy/stylesheet.css';
/* ==================================
	Common styles
/* =================================*/
:root {
  --ff-gilroy: "Gilroy", "sans-serif";
  --bg-def-dark: #0E0E0F;
  --bg-def-dark2: #1A1A1A;
  --bg-343434: #343434;
  --bg-F8F8F8: #F8F8F8;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  outline: none;
  text-decoration: none;
  transition: 0.3s all ease;
}

html {
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
}

html,
body {
  height: 100%;
  scroll-behavior: smooth;
}

body {
  font-family: var(--ff-gilroy);
  leading-trim: both;
  text-edge: cap;
  font-weight: 400;
  letter-spacing: normal;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  background-color: var(--bg-def-dark);
}

a,
button {
  cursor: pointer;
}

.wrapper {
  overflow: hidden;
}

.container {
  width: 100%;
  max-width: 1394px;
  margin: 0 auto;
}

.btn {
  border: none;
  padding: 15px 30px;
  background-color: var(--bg-F8F8F8);
  border-radius: 8px;
  color: var(--bg-def-dark2);
  leading-trim: both;
  text-edge: cap;
  font-size: 17px;
  font-weight: 500;
  line-height: 100%;
  cursor: pointer;
}

/*======================
	Header
=====================*/
header {
  padding-top: 20px;
}
header .header-wrapper {
  border-radius: 14px;
  width: 100%;
  background-color: var(--bg-def-dark2);
  padding: 13px 34px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.nav-links {
  display: flex;
  align-items: center;
  list-style: none;
  column-gap: 35px;
}
.nav-links li a {
  display: flex;
  align-items: center;
  column-gap: 6px;
  color: #F8F8F8;
  leading-trim: both;
  text-edge: cap;
  font-size: 17px;
  font-weight: 500;
  line-height: 26px;
}

.navbar-btns {
  display: flex;
  align-items: center;
  column-gap: 10px;
}

.lang-dropdown .dropdown-menu {
  display: none;
}

.lang-btn {
  background-color: var(--bg-343434);
  color: var(--bg-F8F8F8);
  text-align: center;
  leading-trim: both;
  text-edge: cap;
  font-size: 17px;
  font-weight: 500;
  text-transform: uppercase;
}

.navbar-logo {
  display: block;
  overflow: hidden;
  height: 50px;
  display: flex;
  align-items: center;
}
.navbar-logo img {
  width: 100px;
  height: 70.91px;
  flex-shrink: 0;
}

/*=======================
	Home
======================*/
#home {
  margin-top: 25px;
}

.home-wrap {
  border-radius: 14px;
  width: 100%;
  padding: 55px 0;
  color: var(--bg-F8F8F8);
  background-color: var(--bg-def-dark2);
  background-image: url("../images/home-bg1.svg"), url("../images/home-bg2.svg");
  background-repeat: no-repeat, no-repeat;
  background-position: top left, top right;
  background-size: contain, contain;
}
.home-wrap h1 {
  color: #F8F8F8;
  font-size: 56px;
  font-weight: 600;
  line-height: 68px;
}
.home-wrap h1,
.home-wrap p {
  margin: 0 auto;
  max-width: 651px;
  text-align: center;
}
.home-wrap p {
  margin: 50px auto;
  color: #999;
  font-size: 18px;
  font-weight: 500;
  line-height: 26px;
}
.home-wrap .home-btn {
  display: block;
  width: 100%;
  max-width: 288px;
  text-align: center;
  margin: 0 auto;
}

.home-cards {
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
  gap: 28px;
  margin-top: 44px;
}

.home-card {
  background-color: var(--bg-def-dark2);
  padding: 35px;
  border-radius: 14px;
  width: 100%;
  max-width: calc(50% - 14px);
  height: 100%;
}
.home-card .card-img {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  column-gap: 23px;
}
.home-card .home-card__about {
  color: var(--bg-F8F8F8);
}
.home-card .home-card__about h2 {
  font-size: 26px;
  font-weight: 600;
  line-height: 26px;
  margin-bottom: 20px;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
.home-card .home-card__about p {
  color: #999;
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
.home-card:first-child, .home-card:nth-child(2) {
  height: 170px;
  display: flex;
  align-items: center;
  column-gap: 40px;
}
.home-card:nth-child(3), .home-card:last-child {
  height: 345px;
}
.home-card:nth-child(3) .card-img {
  margin-bottom: 43px;
}
.home-card:last-child .card-img {
  margin-top: 43px;
}

